body {
  margin: 0;
  font-family: Arial, sans-serif;
}

.app {
  color: #fff;
  text-align: center;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  min-height: 100vh;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  animation: background-fade 3s infinite alternate;
}

.main-container {
  background-color: rgba(0, 0, 0, 0.7);
  margin: auto;
  padding: 20px;
  border-radius: 10px;
  max-width: 800px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
}

h1 {
  margin-top: 0;
  font-size: 2.5rem;
}

form {
  margin-bottom: 20px;
}

select,
input {
  margin: 5px;
  padding: 10px;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
}

button {
  margin: 5px;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
  cursor: pointer;
  background-color: #007bff;
  color: #fff;
  transition: all 0.3s ease;
}

button:hover {
  background-color: #0056b3;
  transform: scale(1.05);
}

button:active {
  background-color: #003f7f;
}

.button-group {
  margin-bottom: 20px;
}

.error {
  color: red;
  font-size: 1rem;
  margin-top: 10px;
}


.gallery {
  display: grid;
  gap: 10px;
  padding: 10px;
}

.gallery[data-image-count="1"] {
  grid-template-columns: 1fr;
}

.gallery[data-image-count="2"] {
  grid-template-columns: 1fr 1fr;
}

.gallery[data-image-count="3"] {
  grid-template-columns: repeat(3, 1fr);
}

.gallery[data-image-count="4"] {
  grid-template-columns: repeat(4, 1fr);
}

.gallery[data-image-count="5"] {
  grid-template-columns: repeat(5, 1fr);
}

.gallery-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 5px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.image-card:hover .gallery-image {
  transform: scale(1.1);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.4);
}

img {
  width: 500px;
  height: 500px;
  object-fit: cover;
  border: 5px solid white;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
}

input[type="text"] {
  width: 60%;
  padding: 10px;
  border: 2px solid #fff;
  border-radius: 5px;
  font-size: 1rem;
  background-color: rgba(255, 255, 255, 0.2);
  color: #fff;
  transition: border-color 0.3s ease;
}

input[type="text"]::placeholder {
  color: #ccc;
}

input[type="text"]:focus {
  border-color: #007bff;
  outline: none;
}


.no-breed-message {
  color: #ff6961;
  font-size: 1.2rem;
  margin-top: 10px;
}


@media (max-width: 768px) {
  h1 {
    font-size: 2rem;
  }

  .main-container {
    width: 90%;
  }

  button {
    font-size: 0.9rem;
  }

  .gallery[data-image-count="2"] {
    grid-template-columns: 1fr;
  }

  .gallery[data-image-count="3"],
  .gallery[data-image-count="4"],
  .gallery[data-image-count="5"] {
    grid-template-columns: repeat(2, 1fr);
  }

  img {
    width: 100%;
    height: auto;
  }
}
