.gallery-container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .main-image-container {
    position: relative;
    width: 80%;
    max-width: 600px;
  }
  
  .main-image {
    width: 100%;
    height: auto;
    border-radius: 10px;
    transition: transform 0.3s ease;
  }
  
  .arrow-button {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background: rgba(0, 0, 0, 0.5);
    border: none;
    color: white;
    font-size: 2rem;
    padding: 5px 15px;
    cursor: pointer;
    border-radius: 50%;
    opacity: 0;
    transition: opacity 0.3s ease;
  }
  
  .left-arrow {
    left: 10px;
  }
  
  .right-arrow {
    right: 10px;
  }
  
  .main-image-container:hover .arrow-button {
    opacity: 1;
  }
  
  .preview-container {
    display: flex;
    gap: 10px;
    overflow-x: auto;
    margin-top: 15px;
    padding: 10px;
  }
  
  .preview-image {
    width: 60px;
    height: 60px;
    object-fit: cover;
    border-radius: 5px;
    cursor: pointer;
    border: 2px solid transparent;
    transition: transform 0.3s ease, border-color 0.3s ease;
  }
  
  .preview-image.selected {
    border-color: #007bff;
    transform: scale(1.1);
  }
  
  .preview-image:hover {
    transform: scale(1.1);
  }
  